html {
  @include font(10px, 10px, 400);

  @media (max-width: $desktop) {
    @include font(8px, 8px, 400);
  }

  @media (max-width: $laptop) {
    @include font(7px, 7px, 400);
  }

  @media (max-width: $tablet) {
    @include font(10px, 10px, 400);
  }
}

body {
  @include font(1.6rem, 1.4, 400);
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: $montserrat;
  color: $white;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  background-color: $black;
  font-weight: 400;

  @media (max-width: $laptop) {
    // шаблон медиа-запроса desktop-first
  }

  @media (min-resolution: $extra) {
    // шаблон для фонового изображения на экранах с повышенной плотностью пикселей
  }
}

.gradient {
  max-width: initial !important;
}

button {
  font-family: $montserrat;
}

body,
*,
*::before,
*::after {
  box-sizing: border-box;
}

main {
  position: relative;
  flex-grow: 1;
}

.main.container{
  background-color: $white;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.grecaptcha-badge {
  opacity: 0;
  pointer-events: none;
}


.bg {
  max-width: initial;
  position: absolute;
  top: -31%;
  left: -107%;
  z-index: 1;
  width: 348%;

  @media (max-width: $tablet) {
    width: 230%;
    top: -8%;
    left: -48%;
  }

  @media (max-width: $mobile) {
    width: 450%;
    top: -5%;
    left: -103%;
  }

}


