.main {
  &__top {
    position: relative;

    & > .decor {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 50%;
      transform: translateX(-50%);
      pointer-events: none;
      user-select: none;
      mix-blend-mode: multiply;
      z-index: 6;


      &:before {
        position: absolute;
        content: '';
        left: 20rem;
        top: 0;
        height: 100%;
        width: 16.1rem;
        background-color: $turq;
        z-index: 6;
      }
    }
  }

  &__bg-hover{
    font-size: 0;
    line-height: 0;
    position: absolute;
    z-index: 6;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    transform: scale(0.189) translateX(calc(calc(calc(100% - 1160px)/2)));
    transform-origin: 71.4% 43.3%;
    background: transparent;
    user-select: none;

    &.active + div {
      transform: scale(1) translateX(0);


      &:after {
        opacity: 1;
      }
    }
  }

  &__bg {
    font-size: 0;
    line-height: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    right: 0;
    transform: scale(0.189) translateX(calc(calc(calc(100% - 1160px)/2)));
    transform-origin: 71.4% 43.3%;
    transition: all 0.3s ease-in;
    z-index: 4;

    img {
      width: 100%;
      height: auto;
    }

    // &:after {
    //   content: '';
    //   position: absolute;
    //   transition: all 0.3s ease-in;
    //   top: 0;
    //   left: 0;
    //   opacity: 0;
    //   width: 100%;
    //   height: 100%;
    //   background: linear-gradient(0deg, rgba(0, 0, 0, 0.80) 0%, rgba(0, 0, 0, 0.80) 100%);
    //   background-size: cover;

    //   img {
    //     height: 100%;
    //   }
    // }



  }
}
